import { createRouter, createWebHistory } from 'vue-router';
import axios from 'axios';   


const router = createRouter({
    history: createWebHistory(),
    routes: [
        {
            path: '/',
            name: 'Home',
            component: () => import('../page/HomePage.vue'),
        },
        {
            path: '/login',
            component: () => import('../page/AccountPage.vue')
        },
        {
            path: '/cours',
            name: 'Cours',
            props: true,
            component: () => import('../page/CoursPage.vue')
        },
        {
            path: '/admin',
            name: 'mod',
            component: () => import('../page/ModPage.vue'),
            meta: {
                requiresAuth: true // Add this meta field to require authentication for this route
            },
            children: [
                {
                    path: '/admin/test',
                    name: 'admin',
                    component: () => import('../page/HomePage.vue')
                },
                {
                    path: '/admin/oui',
                    name: 'oui',
                    component: () => import('../page/CoursPage.vue')
                }
            ]
        }
    ]
})

router.beforeEach(async (to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (await isMod() == true) {
            next()            
        } 

        else {
            next({ name: 'Home' })
        }
    } else {
        next()
    }
})

function isLoggedIn() {
    return document.cookie.includes('session=');
}

