async function isMod() {
    if (isLoggedIn() == true) {
        const value = `; ${document.cookie}`;
        let token = value.split(`; session=`);
        if (token.length === 2) {
            token = token.pop().split(';').shift();
        }
        axios.defaults.headers.common = {'Authorization': `Bearer ${token}`}
        try {
            await axios.get('/api/mod-cours', {
                withCredentials: true
            })
            return true;
        } catch (err) {
            return false;
        }
    } else {
        return false;
    }
}

export default router;